import TourNewUserStep from './TourNewUserStep.vue'
export default {
  components: { TourNewUserStep },
  props: {
    tour: { type: Object, required: true }
  },
  mounted () {
    this.$nuxt.$on(
      'sector-expanded',
      () => this.tour.nextStep()
    )
  },
  beforeDestroy () {
    this.$nuxt.$off('sector-expanded')
  }
}
